

import Vue from 'vue'
import App from './App'
import Vuex from 'vuex'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import FastClick from 'fastclick'
import router from './router'
import Native from './native'
import Directive from './directive'
import Filter from './filter'
import store from './store'
import * as constant from './constant'

Vue.use(Vuex);
Vue.use(AlertPlugin);
Vue.use(ToastPlugin);
Vue.use(LoadingPlugin);
Vue.use(ConfirmPlugin);

new Vue({
el: '#app',
router,store,
template: '<App/>',
components: { App }
})


FastClick.attach(document.body);


if(constant.PRODUCTION_ENV == 2){
require('es6-promise').polyfill();
}


if(constant.PRODUCTION_ENV == 0){

var time = 0
if(constant.isIOS) time = 50;
setTimeout( () => {
Native.method("getPage",function (response) {
response = JSON.parse(response);





if(response.type == 0){
var tmp = {path:"/activity/detail",query:{
id:response.id
}}
if(response.code){
tmp.query.code = response.code;
}
router.push(tmp);

store.commit("DEVICE_UUID",{uuid:response.deviceUUID})

store.commit("USER_TOKEN",{token:response.token})
store.commit("USER_POSITION",{
x:response.longitude,
y:response.latitude,
})
}else if(response.type == 1){

router.push({path:"/agreement/user"});
}else if(response.type == 2){

router.push({path:"/agreement/privacy"});
}else if(response.type == 3){

var tmp = {path:"/newInfomation",query:{
id:response.id
}}
if(response.code){
tmp.query.code = response.code;
}
router.push(tmp);

store.commit("DEVICE_UUID",{uuid:response.deviceUUID})

store.commit("USER_TOKEN",{token:response.token})
store.commit("USER_POSITION",{
x:response.longitude,
y:response.latitude,
})
}else{
alert("type为空");
}

})
},time)
}









