import bgImg from '../assets/image/downloadBG.png'
import * as constant from '@/constant'
export default {
    name: "welcome_to_app",
    data() {
        return {
            bgImg: bgImg,
        }
    },
    methods: {
        download() {
            if(!navigator.userAgent.match(/ipad|iphone|ipod|ios/i)){
                //此操作会调起app并阻止接下来的js执行
                window.location = constant.ANDROID_DOWNLOAD_URL
            }else{
                //ios
                window.location = constant.IOS_DOWNLOAD_URL
            }
        }
    }
}



